.company {
  color: #e34f0b;
  font-family: Franklin Gothic Medium;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;

  .name {
    filter: drop-shadow(0.01rem 0.01rem 0.06rem #f8eabd);
    font-size: 1rem;
    letter-spacing: 0.1rem;
    margin-block-start: 0;
    margin-block-end: 0;
  }

  .logo {
    filter: drop-shadow(0.01rem 0.01rem 0.1rem #252525);
    height: 3.3rem;
  }
}
