.join_us {
  display: flex;
  flex-direction: column;
  position: relative;
  align-self: center;

  .title {
    font-size: 1rem;
    position: relative;
    text-align: center;
    padding: 0.3rem;
    background: #76808d;
    border-radius: 0.5rem;
    color: white;
  }

  .container {
    display: flex;
    align-items: flex-end;
    flex-direction: row;
    justify-content: center;

    .text {
      font-family: strait;
      padding: 1rem;
      background-color: #80808070;
      box-shadow: 0.1rem 0.1rem 0.5rem #757086ef;
      width: 45vw;
      border-radius: 0.4rem;
      position: relative;
      font-size: 0.8rem;
      color: #0c0c46;
      margin-block-end: 0;
    }

    .picture {
      filter: drop-shadow(0.1rem 0.1rem 0.2rem #616163ef);
      position: relative;
      height: 11rem;
      right: 0;
    }
  }
}
