.slideshow {
  margin-top: 5rem;
  background: rgb(166 159 159 / 80%);
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  height: 20rem;
  border-radius: 2rem;

  .title {
    font-size: 1.5rem;
    width: 40vw;
  }

  .text {
    color: rgb(12, 12, 70);
    position: relative;
    font-size: 0.9rem;
    font-family: kanit;
    width: 35vw;
  }

  .image {
    box-shadow: 0rem 0.1rem 0.2rem 0.8rem rgb(255 255 255 / 50%);
    background-color: rgba(41, 33, 33, 0.77);
    position: absolute;
    left: 0vw;
    height: 16rem;
    margin: 2rem 4vw 2rem 4vw;
    border-radius: 0.5rem;
  }
}
