.we_are {
  position: relative;
  display: flex;
  align-self: center;
  flex-direction: column;

  .title {
    font-size: 1rem;
    position: relative;
    text-align: center;
    padding: 0.3rem;
    background: #76808d;
    border-radius: 0.5rem;
    color: white;
  }

  .name {
    text-align: center;
    font-size: 1.2rem;
    color: #e34f0b;
    font-family: Franklin Gothic Medium;
    letter-spacing: 0.2rem;
  }

  .text {
    font-family: strait;
    border-radius: 0.4rem;
    color: #0c0c46;
    background: #8080806f;
    box-shadow: 0rem 0rem 0.1rem 0.2rem #fffefc;
    font-size: 0.8rem;
    width: 70vw;
    padding: 1rem;
  }
}
