.job_offer {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: strait;
  background: linear-gradient(to left,#7c7c7e4f, #a8ccff, #7c7c7e4f);
  margin-top: 1rem;
  box-shadow: 0 0.1rem 0.2rem #f8f7b1, 0 -0.1rem 0.2rem #fae7c48e;
  border-radius: 20%;

  .arrow {
    margin-right: 1vw;
    font-size: 1.2rem;
    color: #06067e;
  }

  .title {
    font-size: 1.2rem;
  }

  .image {
    height: 8rem;
    filter: drop-shadow(0 0.1em 0.2rem #2f2f30);
  }

  .text {
    width: 90vw;
    font-size: 0.9rem;
  }

  .list {
    list-style: none;

    .item {
      width: 60vw;
      font-size: 0.9rem;
    }
  }
}
