.trading {
  display: flex;
  flex-direction: column;
  position: relative;
  align-items: center;

  .name {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-size: 1.2rem;
    color: #443665;
    letter-spacing: 0.2rem;
  }
  .text {
    margin-block-start: 0;
    font-family: strait;
    border-radius: 0.4rem;
    color: #0c0c46;
    background: linear-gradient(to top, #dce1ff, #f4f4f8);
    box-shadow: 0rem 0rem 0.1rem 0.1rem #e0dee4;
    border-top: solid 0.2rem;
    border-color: #76808d;
    border-top-left-radius: 10%;
    border-bottom-right-radius: 10%;
    font-size: 0.8rem;
    width: 80vw;
    padding: 1rem;
  }
  .subtitle {
    font-size: 1rem;
    color: #e34f0b;
    letter-spacing: 0.1rem;
  }
  .subtext {
    width: 80vw;
    font-family: strait;

    strong {
      text-transform: uppercase;
      font-size: 1rem;
      color: #0c0c46;
      font-family: kanit;
    }
  }
  .end {
    margin-block-start: 0;
    font-family: strait;
    border-radius: 0.4rem;
    color: #0c0c46;
    background: linear-gradient(to top, #dce1ff, #f4f4f8);
    box-shadow: 0rem 0rem 0.1rem 0.1rem #e0dee4;
    border-bottom: solid 0.2rem;
    border-color: #76808d;
    border-bottom-right-radius: 10%;
    font-size: 1rem;
    width: 80vw;
    padding: 1rem;
    font-family: kanit;
  }
}
