.our_mission {
  display: flex;
  flex-direction: column;
  position: relative;
  font-family: strait;
  align-items: center;

  .name {
    font-family: kanit;
    text-align: center;
    font-size: 1.2rem;
    color: #e34f0b;
    border-bottom: 0.1rem solid #e34f0b;
  }

  .text {
    font-family: strait;
    border-radius: 0.4rem;
    color: #0c0c46;
    background: #8080806f;
    box-shadow: 0rem 0rem 0.1rem 0.2rem #fffefc;
    font-size: 0.8rem;
    width: 70vw;
    padding: 1rem;
  }

  .identification {
    filter: drop-shadow(0.1rem 0.2rem 0.1rem #3a3636);
    position: absolute;
    height: 6rem;
    left: 17vw;
    top: 0rem;
  }
}
