.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;
  text-align: center;
  width: 70vw;
  position: relative;
  top: 3rem;
  box-shadow: 0.1rem 0.01rem 0.1rem 1rem #76808d;
  background: #f9faee;
  border: 0.9rem solid white;
  border-radius: 0.5rem;
  color: #0a0a0a;

  .title {
    position: relative;
    font-size: 1rem;
  }

  .label {
    position: relative;
    color: #0c0c46;
    font-family: strait;
    font-weight: bold;
    text-shadow: 0.01rem 0.7rem 0.4rem #f0de99;
    font-size: 0.8rem;
  }

  .input {
    margin: 0.4rem 0vw 0.4rem 0vw;
    padding: 0.4rem;
    width: 53vw;
    background-color: #ffc964;
    border: 0.1rem solid #76808d;
    box-shadow: 0.01rem 0.05rem 0.1rem 0.1rem #383733bb;
    border-radius: 0.3rem;
  }

  .message {
    margin: 0.6rem;

    &.input {
      margin: 0;
      width: 53vw;
      height: 5rem;
    }
  }

  .send {
    margin: 1rem;
    position: relative;
    width: 30vw;
    padding: 0.4rem;
    cursor: pointer;
    border: none;
    background: #76808d75;
    color: #7e7a7a;
    border-radius: 0.5rem;
    font-family: kanit;
    font-size: 0.9rem;

    &:hover {
      background-color: #363450;
      color: #ffffff;
      filter: drop-shadow(0 0.1rem 0.1rem #363636);
    }
  }
}
