.to_work {
  margin: 1rem 2vw 1rem 2vw;
  display: flex;
  flex-direction: column;
  align-items: center;

  .text {
    position: relative;
    left: 14vw;
    width: 35vw;
    text-align: end;
    color: rgb(12, 12, 70);
    border-top: solid 0.3rem rgb(252, 147, 55);
    border-bottom: solid 0.3rem rgb(252, 147, 55);
    padding: 4rem 6vw 4rem 17vw;
    letter-spacing: 0.04rem;
  }

  .arrow {
    position: absolute;
    width: 15rem;
    filter: drop-shadow(0rem 0.1rem 0.2rem grey);

    &.left {
      margin-top: 9rem;
      left: 6vw;
      transform: rotate(-50deg);
    }

    &.right {
      margin-top: 38rem;
      right: 10vw;
      transform: scaleX(-1) rotate(-60deg);
    }
  }

  .button {
    margin: 2rem 0vw 2rem 0vw;
    position: relative;
    width: 48vw;
    right: 20vw;
    transition: 0.5s ease-in-out;

    &:hover {
      transform: scale(1.2);
    }
  }
}
