.to_contact {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 90%;
  margin: 4rem 2vw 2rem 2vw;

  .image {
    margin-right: 2vw;
  }

  .title {
    margin-block-start: 0;
    margin-block-end: 0;
    padding: 0.5rem 3vw 0.5rem 3vw;
    border: solid 0.3rem;
    position: relative;
    font-size: 2.5rem;
    color: #695045;
    font-family: kanit;
  }

  .text {
    position: relative;
    left: 4vw;
    text-decoration: none;
    font-family: 'Strait';
    clip-path: polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%);
    background-color: #e9b552;
    padding: 3rem;
    margin: 2rem;
    color: #0c0c46;
    font-size: 1.3rem;
    text-align: start;
    border-radius: 1rem;

    &:hover {
      color: rgb(254, 255, 255);
      background: linear-gradient(45deg, rgba(255, 193, 7, 1) 0%, rgba(255, 87, 34, 1) 100%);
      transform: scale(1.03);
      transition: 0.7s ease-out;
    }
  }
}

.phone {
  font-size: 5rem;
  color: #1dac1d;

  &:hover {
    animation: scale 0.8s ease infinite;
    color: #0ae30a;

    @keyframes scale {
      0% {
        transform: scale(1);
      }
      50% {
        transform: scale(1.2);
      }
      100% {
        transform: scale(1);
      }
    }
  }
}
