.navigation {
  position: relative;
  font-size: 0.7rem;
  bottom: 1rem;
  display: flex;
  flex-direction: row;
  font-family: kanit;
  text-transform: uppercase;

  .indicator {
    border-radius: 0.4rem;
    transition: 0.2s ease-out;

    &.active {
      background-color: #ffffff42;
      box-shadow: 0 -0.15rem 0.1rem #e34f0b;
    }

    .item {
      display: flex;
      align-content: center;
      margin: 0.4rem;

      .link {
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 15vw;
        padding: 0.4rem 0;
        background: linear-gradient(to top, #616188, #0d0d0ecb);
        text-shadow: 0.1rem 0.07rem 0.8rem #0d1213;
        text-align: center;
        text-decoration: none;
        color: white;
        box-shadow: 0rem 0.2rem 0.5rem 0.06rem #9a9090a4;
        position: relative;
        overflow: hidden;
        border-radius: 0.2rem;
        transition: height 0.3s, transform 0.3s;
        transition-delay: 0.15s;

        &:before {
          content: "";
          position: absolute;
          width: 100%;
          height: 0;
          top: 0;
          left: 0;
          box-shadow: 0.01rem 0.01rem 0rem 0.1rem #ffffff;
          background: linear-gradient(90deg, #f8f7f245, #ffffff22);
          transition: height 0.35s;
        }

        &:hover {
          transform: translateY(0.3rem);

          &:before {
            height: 100%;
          }
        }
      }
    }
  }
}
