@import url('https://fonts.googleapis.com/css2?family=Strait&display=swap');

body {
  margin: 0;
  padding: 0;

  header {
    background: linear-gradient(to bottom, #a8ccff, #8872f8);
    box-shadow: 0rem 0.1rem 0.4rem #444d4942;
    border-radius: 0.4rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 2;
    height: 9rem;
  }

  main {
    background: white;
    font-family: Kanit;
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 0;
  }

  footer {
    background: #bbbcbe;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    position: relative;
    z-index: 1;
    border-radius: 0.4rem;
    margin-top: 5rem;

    .social {
      position: absolute;
      margin-top: 0.2rem;
      right: 2vw;
      font-size: 2rem;
      color: #1c1cf1;
      z-index: 1;

      &:hover{
        color: #0a0a97;
      }
    }
  }
}
