.our_values {
  display: flex;
  flex-direction: column;
  position: relative;
  align-items: center;
  margin-top: 2rem;
  background: linear-gradient(to top, #dce1ff, #cacafc);

  .name {
    font-family: kanit;
    text-align: center;
    font-size: 1.2rem;
    color: #e34f0b;
    border-bottom: 0.1rem solid #e34f0b;
  }

  .text {
    font-family: strait;
    border-radius: 0.4rem;
    color: #0c0c46;
    background: linear-gradient(to top, #dce1ff, #f4f4f8);
    border: solid 0.15rem #6b635f;
    box-shadow: 0rem 0rem 0.1rem 0.3rem #fffefc;
    font-size: 0.8rem;
    width: 70vw;
    padding: 1rem;
  }

  .left {
    font-size: 1.2rem;
    color: #494747;
    margin-block-start: 0;
    margin-block-end: 0;
  }
}
