.why_choice {
  display: flex;
  flex-direction: column;
  position: relative;
  align-items: flex-start;
  background: linear-gradient(to bottom, #dce1ff, #cacafc);

  .name {
    margin: 2rem 3vw;
    font-size: 1.2rem;
    text-transform: uppercase;
    color: #e34f0b;
    border-bottom: 0.1rem solid #e34f0b;
    font-family: kanit;
    font-size: 1.3rem;
    color: #e34f0b;
  }

  .man {
    filter: drop-shadow(0.1rem 0.2rem 0.1rem #d3c6c6);
    position: absolute;
    height: 6rem;
    right: 3vw;
    top: 0;
  }

  .text {
    align-self: center;
    font-family: strait;
    border-radius: 0.4rem;
    color: #0c0c46;
    background: linear-gradient(to top, #dce1ff, #f4f4f8);
    border: solid 0.15rem #6b635f;
    box-shadow: 0rem 0rem 0.1rem 0.3rem #fffefc;
    font-size: 0.8rem;
    width: 70vw;
    padding: 1rem;
    margin-bottom: 2rem;
  }

  .right {
    font-size: 1.2rem;
    color: #553829;
    margin-left: 3vw;
    margin-block-start: 0;
    margin-block-end: 0;
  }
}
