.recruitment {
  position: relative;
  display: flex;
  align-self: center;
  flex-direction: column;
  
  .title {
    font-size: 1rem;
    position: relative;
    text-align: center;
    padding: 0.3rem;
    background: #76808d;
    border-radius: 0.5rem;
    color: white;
  }

  .name {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-size: 1.2rem;
    color: #443665;
    letter-spacing: 0.2rem;

  .image {
    box-shadow: 0 0.1rem 0.2rem #817c7c;
    position: relative;
    background: #f7c791;
    height: 8rem;
    border-top: solid 0.2rem;
    border-bottom: solid 0.2rem;
    border-color: #fad2bf;
    border-top-right-radius: 10%;
    border-bottom-left-radius: 10%;
  }
}

  .text {
    margin-block-start: 0;
    font-family: strait;
    border-radius: 0.4rem;
    color: #0c0c46;
    background: linear-gradient(to top, #dce1ff, #f4f4f8);
    box-shadow: 0rem 0rem 0.1rem 0.1rem #e0dee4;
    border-top: solid 0.2rem;
    border-bottom: solid 0.2rem; 
    border-color: #76808d;
    border-top-left-radius: 10%;
    border-bottom-right-radius: 10%;
    font-size: 0.8rem;
    width: 80vw;
    padding: 1rem;
  }
}
