.welcome {
  &.one {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    margin: 4rem 0vw 2rem 0vw;

    .title {
      position: absolute;
      top: -2rem;
      left: 5vw;
      text-align: center;
      color: #0c0c46;
      font-size: 1rem;
      z-index: 1;
    }

    .city {
      position: relative;
      width: 55vw;
    }
  }

  &.two {
    margin: -1rem 2vw 1rem 2vw;
    padding: 1rem 10vw 1rem 10vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    background: linear-gradient(to right, #99b8e2, #e1e2e4, #99b8e2);
    clip-path: polygon(50% 0, 90% 0, 100% 50%, 90% 100%, 10% 100%, 0 50%, 10% 0);
    z-index: 1;

    .text {
      filter: drop-shadow(0.01rem 0.01rem 0.06rem #f6f6cf);
      color: #0c0c46;
      font-size: 0.9rem;
      text-align: center;
      margin: 1rem 0vw;
    }

    .office {
      box-shadow: 0.1rem 0rem 0.6rem black;
      width: 50vw;
    }
  }
}
