.content {
  display: flex;
  flex-direction: column;
  position: relative;
  font-family: kanit;
  color: #222222;
  text-align: center;

  .title {
    letter-spacing: 0.1rem;
    font-family: "kanit";
    font-size: 1rem;
    color: #e34f0b;
  }

  .address,
  .info {
    font-size: 0.9rem;
  }

  .address {
    text-align: center;
    font-family: "kanit";
  }

  .info {
    font-family: strait;
  }
}

.copyright {
  font-family: "kanit";
  color: #222222;
  text-align: center;
  font-size: 0.7rem;

  .link {
    color: #222222;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }
}
